                <div class=" py-5 w-full z-100  xl:px-36">

                    <ul class="list-none text-center text-xl flex-grow justify-center flex flex-col md:flex-row xl:gap-4 xl:text-xl">
                        <li @click="goToHome()" class="flex-1   cursor-pointer nav-item text-yellow-500">Beranda</li>
                        <li @click="goToBerita()"
