<!-- end: navbar sementara -->
</template>

<script>
export default {
    name: 'NavBar',
    methods: {
        goToHome() {
            return this.$router.push('/');
        },
        goToBerita() {
            return this.$router.push('/Berita');
        },
        goto(refName) {
            var element = this.$refs[refName];
            var top = element.offsetTop;

            window.scrollTo(0, top);
        }
    }
}
</script>