import { createRouter, createWebHistory } from 'vue-router';
import Landing from './components/Main.vue';
import Konten1 from './components/Secondary_1.vue';
import Berita from './components/Trinary.vue';


const routes = [
    {
        // path: '/lombapramuka',
        path: '/',

        name: 'Landing',
        components:{
            
            Landing,
            
        }
    },
    {
        // path: '/lombapramuka/Berita',
        path: '/Berita',
        name: 'Berita',
        components:{
            Berita,
        }
    },
    {
        // path: '/lombapramuka/Konten1',
        path: '/Konten1',
        name: 'Konten1',
        components:{
             Konten1
        }
    }    
]

export const router = createRouter({
    history: createWebHistory(),
    routes
  })
  